.detail {
  background-color: white;
  margin-right: auto;
  margin-left: auto;
  margin-top: 10%;
  margin-bottom: 70px;
  width: 50%;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.detail img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.detail .movie__title,
.detail .movie__year {
  margin: 0;
  font-weight: 300;
}

.detail .movie__title {
  margin-bottom: 5px;
  font-size: 30px;
  font-weight: 400;
  color: burlywood;
}

.detail .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}

.detail .movie__genres li,
.detail .movie__year {
  margin-right: 10px;
  font-size: 14px;
}

.detail .movie__summary {
  color: black;
  font-size: 18px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
